import { useState, useEffect } from 'react';
import axios from 'axios';

export default function BookingApp() {
  const [date, setDate] = useState('');
  const [slots, setSlots] = useState([]);
  const [selectedSlot, setSelectedSlot] = useState('');
  const [name, setName] = useState('');
  const [phone, setPhone] = useState('');
  const [message, setMessage] = useState('');

  useEffect(() => {
    if (date) {
      axios.get(`http://localhost:5000/slots?date=${date}`)
        .then(res => setSlots(res.data.availableSlots))
        .catch(err => console.error(err));
    }
  }, [date]);

  const handleBooking = async () => {
    if (!name || !phone || !date || !selectedSlot) {
      setMessage('All fields are required');
      return;
    }

    try {
      await axios.post('http://localhost:5000/book', { name, phone, date, timeSlot: selectedSlot });
      setMessage('Appointment booked successfully!');
      setSlots(slots.filter(slot => slot !== selectedSlot));
    } catch (error) {
      setMessage(error.response?.data?.error || 'Booking failed');
    }
  };

  return (
    <div className="max-w-md mx-auto p-4 bg-white rounded-lg shadow-md">
      <h2 className="text-xl font-bold mb-4">Book an Appointment</h2>
      <input type="date" className="w-full p-2 border rounded mb-2" value={date} onChange={e => setDate(e.target.value)} />
      <select className="w-full p-2 border rounded mb-2" onChange={e => setSelectedSlot(e.target.value)}>
        <option value="">Select a time slot</option>
        {slots.map(slot => <option key={slot} value={slot}>{slot}</option>)}
      </select>
      <input type="text" placeholder="Your Name" className="w-full p-2 border rounded mb-2" value={name} onChange={e => setName(e.target.value)} />
      <input type="text" placeholder="Phone Number" className="w-full p-2 border rounded mb-2" value={phone} onChange={e => setPhone(e.target.value)} />
      <button className="w-full bg-blue-500 text-white p-2 rounded" onClick={handleBooking}>Book Appointment</button>
      {message && <p className="mt-2 text-center text-red-500">{message}</p>}
    </div>
  );
}
